export class BasicObject {
    constructor(config, imageService, canvasService) {
        this.config = config;
        this.imageService = imageService;
        this.canvasService = canvasService;
        this.object = Object.assign(Object.assign({}, config), { model: this.imageService.files[config.modelName] });
    }
    drawObject() {
        const modelCoords = [
            ...this.config.coords, ...this.config.modelSize
        ];
        this.canvasService.drawImage(this.object.model, modelCoords);
    }
}
